*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.container {
  max-width: 272px;
  margin-left: auto;
  margin-right: auto;
  padding: 50px 0;
}

@media screen and (min-width: 595px) {
  .container {
    width: 595px;
    max-width: 668px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: none;
    width: 1110px;
  }
}

body {
  color: #fff;
  background-color: #0e0e0e;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

img {
  height: auto;
  width: 100%;
  max-width: 100%;
  display: block;
}

input:focus {
  outline: none;
}

.modal-backdrop {
  width: 100%;
  height: 100%;
  backdrop-filter: blur(2px);
  opacity: 1;
  z-index: 2;
  background-color: rgba(0, 0, 0, .6);
  justify-content: center;
  align-items: center;
  padding: 20px;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.modal-backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  width: 100%;
  max-width: 272px;
  max-height: none;
  color: #000;
  background-color: #fff2fd;
  border-radius: 150px 0;
  margin: auto;
  padding: 75px 12px 104px;
  position: relative;
  top: auto;
  left: auto;
  transform: none;
}

@media screen and (min-width: 595px) {
  .modal {
    max-width: 595px;
    border-radius: 300px 0;
    padding: 28px 0 56px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    max-width: 942px;
  }
}

.btn-modal-close {
  width: 25px;
  height: 25px;
  fill: #5f00d9;
  cursor: pointer;
  background: none;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 30px;
  right: 30px;
}

.btn-modal-close svg {
  width: 17px;
  height: 17px;
}

.btn-modal-close:hover, .btn-modal-close:focus {
  fill: #3c00c8;
}

.modal-artist-preview {
  width: 102px;
  height: 102px;
  object-fit: cover;
  border-radius: 51px;
  margin: 0 auto 18px;
}

.modal-main {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  display: flex;
}

@media screen and (min-width: 595px) {
  .modal-main {
    flex-wrap: nowrap;
  }
}

.modal-info {
  text-align: center;
  flex-direction: column;
  gap: 10px;
  display: flex;
}

@media screen and (min-width: 595px) {
  .modal-info {
    width: 40%;
    text-align: left;
  }
}

@media screen and (min-width: 1200px) {
  .modal-info {
    gap: 20px;
  }
}

.modal-title {
  text-transform: uppercase;
  color: #5f00d9;
  margin-bottom: 5px;
  font-size: 20px;
  font-weight: 600;
}

.modal-desc {
  color: #0e0e0e;
}

.modal-artist-main {
  width: 233px;
  height: 416px;
