.modal-tickets {
  color: #fff;
  text-transform: uppercase;
  background-color: #5f00d9;
  border: 1px solid #5f00d9;
  border-radius: 5px;
  margin-top: 5px;
  padding: 10px 19px;
  font-weight: 500;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-tickets:hover, .modal-tickets:focus {
  color: #5f00d9;
  background-color: rgba(0, 0, 0, 0);
}

.modal-more {
  max-width: 230px;
  max-height: 40px;
  margin: 43px auto 0;
  display: block;
}

.modal-more button {
  color: #5f00d9;
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #5f00d9;
  border-radius: 5px;
  padding: 10px 3px;
  font-weight: 500;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

