.hero-title, .hero-inputs {
  z-index: 1;
  position: relative;
}

.countries-set, .search-form {
  position: relative;
}

.countries-set:hover #search-input, .countries-set:hover .input-countries, .countries-set:focus #search-input, .countries-set:focus .input-countries, .search-form:hover #search-input, .search-form:hover .input-countries, .search-form:focus #search-input, .search-form:focus .input-countries {
  border-color: #fff;
}

#search-input, .input-countries {
  height: 40px;
  width: 270px;
  min-width: 190px;
  backdrop-filter: blur(20px);
  color: #939393;
  background: none;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 20px;
  justify-content: center;
  padding: 10px 30px;
  font-size: 16px;
  font-weight: 400;
  transition: border .25s cubic-bezier(.4, 0, .2, 1), border-radius .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: relative;
}

.dropdown-countries, .search-submit {
  height: 20px;
  background: none;
  padding: 0;
  position: absolute;
  top: 10px;
  right: 30px;
}

.search-submit svg {
  width: 12px;
  height: 12px;
  fill: #939393;
}

.dropdown-countries svg {
  width: 15px;
  height: 10px;
  fill: #939393;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.countries-block {
  width: 100%;
  max-height: 0;
  opacity: 0;
  scroll-behavior: smooth;
  backdrop-filter: blur(20px);
  color: #939393;
  background: none;
  border: 1px solid #fff;
  border-radius: 20px;
  transition: max-height .25s cubic-bezier(.4, 0, .2, 1), border-radius .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 100%;
  right: 0;
  overflow: auto;
}

.fi {
  border-radius: 15%;
  margin-right: 10px;
}

.countries-set.is-open .countries-block {
  max-height: 100px;
  opacity: 1;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

@media screen and (min-width: 1200px) {
  .countries-set.is-open .countries-block {
    max-height: 270px;
  }
}

.countries-set.is-open .input-countries {
  border-color: #fff #fff currentColor;
  border-bottom-style: none;
  border-bottom-width: medium;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.countries-set.is-open .dropdown-countries svg {
  transform: rotate(180deg);
}

.country-item {
